
import { defineComponent, ref, reactive, toRefs, onMounted } from "vue";
import videoGameService from '../service/VideoGameService';
import { useGetGames } from "../state/gamesState";

    export default defineComponent({
      name: "AddGameForm",
      setup() {
          const formValues = reactive({
              titleText: '',
              summaryText: '',
              developerText: '',
              publisherText: '',
              ratingVal: 0,
              selectedPlatform: '',
              selectedOwned: '',
              selectedProgress: '',
              imgUrlText: ''
          })
          const platform = ref([
              { name: 'PS4' },
              { name: 'XB1' },
              { name: 'SWTCH' },
              { name: 'PC' }
          ]);
          const owned = ref([
              { name: 'Owned' },
              { name: 'Wish' },
              { name: 'Unowned' }
          ]);
          const progress = ref([
              { name: 'Unplayed' },
              { name: 'In Progress' },
              { name: 'Main Story Completed' },
              { name: '100% Completed' }
          ]);
          const { games } = useGetGames();
          const sendGame = async () => {
              console.log(formValues);
              try {
                  const newGame = await videoGameService.saveVideoGame(formValues);
                  console.log(games)
                  console.log(games.value)
                  if (games.value) {
                      games.value.push(newGame[0]);
                  }
              } catch (e) {
                  console.log(e);
              }
          };
          return {
              ...toRefs(formValues),
              platform,
              owned,
              progress,
              sendGame
          }
      }
    })
